@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;1,300&display=swap');

// utils
@import '../scss/utils/mixins.scss';
@import '../scss/utils/colors.scss';
@import '../scss/utils/button.scss';
@import '../scss/utils/font.scss';
@import '../scss/utils/form.scss';
@import '../scss/utils/grid.scss';
@import '../scss/utils/restructure.scss';
@import '../scss/utils/modal.scss';
@import '../scss/utils/filterbooks.scss';


// components
@import '../scss/cards/bookcard.scss';
@import '../scss/cards/booklist.scss';
@import '../scss/cards/bookgraph.scss';
@import '../scss/cards/bookprogress.scss';
@import '../scss/cards/bookpagecard.scss';

// navigation
@import '../scss/navigation/sidebar.scss';
@import '../scss/navigation/topbar.scss';
@import '../scss/navigation/search.scss';

// themes
@import '../scss/theme/dark.scss';
@import '../scss/theme/light.scss';

body{
    height: 100vh;
	font-family: "open sans" , sans-serif;
}

.star-icon{
	path{
		stroke: #FFDD73;
		fill:#FFDD73;
	}
}


.flex{
	display: flex;
}

.justify-sb{
	justify-content: space-between;
}

.full-width{
	width: 100%;
}

.flex-column{
	flex-direction: column;
}

.v-h-center{
	place-content: center;
	flex-wrap: wrap;
}

.v-center{
	align-items: center;
}

.h-center{
	justify-content: center;
}

.opacity{
	opacity: 0.5;
}

strong{
	font-weight: 500;
}

svg{
	overflow: visible;
	g{
		stroke-width: 1px;
	}
}

.main{
	height: calc(100% - 76px);
	width: calc(100% - 159px);
	position: absolute;
	bottom:0;
	right: 0;
	overflow-y:scroll;

	@include hideScroll('&');

	@media only screen and (max-width:1024px){
        width: calc(100% - 70px);
    }

	@media only screen and (min-width: 320px) and (max-width:923px){
        width: 100%;
    }
}

.hidden{
	display: none;
}

.margin-l-r-20{
	margin: 0 20px;
}

.padding-l-r-20{
	padding: 0 20px;
}

.side-bar-content , .search , .section-title-icon , .book-progress , .book-list{
	svg{
		width: 15px;
	}
}

.top-bar-inner-account{
	svg{
		width: 20px;
	}
}

.section-title{
	text-transform: uppercase;

	& div:nth-child(1){
		gap:5px;
	}

	@include sectionTitle("green" , $primaryGreen);
	@include sectionTitle("red" , $primaryRed);
	@include sectionTitle("purple" , $primaryPurple);
	@include sectionTitle("orange" , $primaryOrange);


	&-icon{
		border-radius: 5px;
		width: 20px;
		height: 20px;
	}
}

.more{
	position: relative;
	flex-direction: column;
	gap: 2px;
	height: 100%;
	height: 20px;
	width: 20px;
	background: rgba($gray , 0.2);
	display: flex;
	flex-wrap: wrap;
	place-content: center;
	border-radius: 100%;

	div{
		width: 2px;
		height: 2px;
		border-radius: 100%;
		background: $gray;
	}
}

.filter-item{
    @include tag;
    @include filteredTag('&.green' , $primaryGreen);
    @include filteredTag('&.red' , $primaryRed);
    @include filteredTag('&.orange' , $primaryOrange);
    @include filteredTag('&.purple' , $primaryPurple);
}

[data-clickable="true"]{
	cursor: pointer;
}

.check-boxes-container{
	flex-wrap: wrap;
	gap:20px;
}

.filter-check{
	gap:5px;
	
	>div{
		position: relative;
		width: 10px;
		height:10px;
		border-radius:2px;
	}

	p{
		text-transform: uppercase;
	}

	svg{
		display: none;
	}

	&.checked{
		.check-mark{
			display: block;
			position: absolute;
			width: 6px;
			margin: auto;
			inset: 0;
			line-height: 0.9;

			polyline{
				stroke-width: 2px;
			}
		}
	}

	@include semiTransparentColouredBorderBox('& > div.red' , $primaryRed);
	@include semiTransparentColouredBorderBox('& > div.green' , $primaryGreen);
	@include semiTransparentColouredBorderBox('& > div.orange' , $primaryOrange);
	@include semiTransparentColouredBorderBox('& > div.purple' , $primaryPurple);
	@include semiTransparentColouredBorderBox('& > div.gray' , $gray)
}